import React from 'react'
import {NavLink} from "react-router-dom"

export default function CartTotals({value}) {

    const {cartSubTotal,cartTax,cartTotal,clearCart} = value
    return (
        <div>
            <div className="container">
                <div className="row">
                    <div className="col-10 mt-2 ml-sm-5 ml-md-auto col-sm-8">
                        <NavLink to="/">
                            <button className="btn btn-outline-danger text-uppercase mb-3 px-5" type="button" onClick={()=> clearCart()}>Clear</button>
                        </NavLink>
                        <h5>
                            <span className="text-title">Subtotal: </span><strong> BDT {cartSubTotal}</strong>
                        </h5>
                        <h5>
                            <span className="text-title">Tax: </span><strong> BDT {cartTax}</strong>
                        </h5>
                        <h5>
                            <span className="text-title">Total: </span><strong> BDT {cartTotal}</strong>
                        </h5>
                    </div>
                </div>
            </div>
        </div>
    )
}
