import React, { Component } from 'react'
import TabletAndroidIcon from '@material-ui/icons/TabletAndroid';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import {NavLink} from "react-router-dom"
import { ButtonContainer } from "./Button"
import styled from 'styled-components';

export default class Navbar extends Component {
    render() {
        return (
            <div>
               <nav class="navbar navbar-expand navbar-light nav_color">
  <NavLink class="navbar-brand ml-5" to=""><TabletAndroidIcon/></NavLink>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>
  <div class="collapse navbar-collapse" id="navbarText">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item ">
        <NavLink class="nav-link ml-1" to="">Phone_Mela-2020</NavLink>
      </li> 
    </ul>
    <NavLink to="/card" className="ml-auto">
        <ButtonContainer className="mr-5 card_sh"> <span className="mr-2"><ShoppingCartIcon /></span>My Cards</ButtonContainer>
    </NavLink>
  </div>
</nav>
            </div>
        )
    }
}





