import React from 'react'
import DeleteIcon from '@material-ui/icons/Delete';

export default function CartItem({item,value}) {

    const {id,title,img,price,count,total} = item
    const {increment,decrement,removeItem} = value
    return (
        <div className="row my-5  text-capitalize text-center">
            <div className="col-12 col-lg-2">
                <img src={img} style={{ width:"5rem", height:"5rem"}} className="img-fluid" alt="product"></img>
            </div>
 
             <div className="col-12  col-lg-2">
                <span className="d-lg-none">product : </span>{title}
            </div>

            <div className="col-12  col-lg-2">
                <span className="d-lg-none">price: </span>{price}
            </div>

            <div className="col-12 col-lg-2   my-2 my-lg-0">
                <div className="d-flex justify-content-center">
                    <div>
                        <span className="btn btn-black mx-1" onClick={() =>decrement(id)}>-</span>
                        <span className="btn btn-black mx-1" >{count}</span>
                        <span className="btn btn-black mx-1" onClick={() =>increment(id)}>+</span>
                    </div>
                </div>
            </div> 
            <div className="col-12 col-lg-2 deleticon" onClick={() => removeItem(id)}><DeleteIcon />
            </div>

            <div className="col-12  col-lg-2">
                <strong>Total item: BDT {total}</strong>
            </div>
        </div>
    )
}
