import React, { Component } from 'react'
import {ProductConsumer} from "../context"
import {NavLink} from "react-router-dom"
import {ButtonContainer} from "./Button"

export default class Details extends Component {
    render() {
        return (
            <ProductConsumer>
                {(value) =>{
                    const {id,company,img,title,inCart,price,info} = value.detailProduct
                    return(
                        <div className="container py-5">
                            <div className="row">
                                <div className="col-10 mx-auto text-center text-slanted text-blue my-5"><h1>{title}</h1></div>
                            </div>
                            <div className="row">
                                <div className="col-10 mx-auto col-md-6 my-3 "><img src={img} className="img-fluid" alt="product"></img></div>
                                <div className="col-10 mx-auto col-md-6 my-3 text-capitalize">
                                    <h3>Model: {title}</h3>
                                    <h4 className="text-title text-uppercase text-muted mt-3 mb-2">
                                        made by: <span className="text-uppercase">{company}</span>
                                    </h4>
                                    <h4 className="text-blue">
                                        <strong>price: <span> BDT</span> {price}</strong>
                                    </h4>
                                    <p className="text-capitalize font-weight-bold mt-3 mb-0">Info about Product</p>
                                    <p className="text-muted lead">{info}</p>
                                    <div>
                                        <NavLink to="/">
                                            <ButtonContainer>
                                                Back to previous products?
                                            </ButtonContainer>
                                        </NavLink>

                                        <ButtonContainer cart disabled={inCart? true:false} onClick={() =>{
                                            value.addToCart(id);
                                            value.openModal(id);
                                        }} >
                                            {inCart?"inCart" : "Add to Cart"}
                                        </ButtonContainer>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }}
            </ProductConsumer>
        )
    }
}
